import React, { useState } from 'react';
import { ReactComponent as LikeIcon } from './svgimg/like.svg';

const LikeButton = ({ likes, postId, onLikeClick, likedBy  }) => {
  const [liked, setLiked] = useState(false);
  const isCurrentUserLiked = likedBy.includes(localStorage.getItem('username'));
  // Set the initial liked status based on whether the current user has liked the post
  useState(() => {
    setLiked(isCurrentUserLiked);
  }, [isCurrentUserLiked]);


  const handleLikeClick = async () => {
    // Toggle the liked state and update the number of likes accordingly
    setLiked(liked);
  
    // Call the parent component's onLikeClick function (if provided)
    if (onLikeClick) {
      await onLikeClick(liked, postId);
    }
    setLiked(!liked)
  };
  

  return (
    // List item representing the like button
    <li onClick={handleLikeClick} style={{ cursor: 'pointer' }}>
      <LikeIcon width="25" height="25" className={liked ? 'liked' : ''} />
      <span style={{ color: liked ? 'blue' : 'black' }}>{likes} Like</span>
    </li>
  );
};

export default LikeButton;
